<template>
    <div class="container">
        <p class="caption">请您提供以下信息</p>
        <ul class="game">
            <li class="active">
                和平精英
            </li>
            <li>
                狂野飙车8
            </li>
            <li>
                实况足球
            </li>
        </ul>
        <div class="area" @click="selectArea">
            <img src="../../assets/img/icon4.png" class="icon1">
            北京 地区
            <img src="../../assets/img/down1.png" class="icon2">
        </div>
        <div class="select" @click="selectShop">
            <img src="../../assets/img/best.png" class="best">
            <span>北京顺义店</span>
            <img class="icon1" src="../../assets/img/down2.png">
        </div>
        <div class="select">
            <img src="../../assets/img/best.png" class="best">
            <span>7 月 16 日,星期二,13:00 - 13:30</span>
            <img class="icon1" src="../../assets/img/date.png">
        </div>
        <div class="label">挑战者姓名</div>
        <div class="formItem">
            <input type="text" placeholder="请输入姓名" />
        </div>
        <div class="label">手机号</div>
        <div class="formItem">
            <input type="text" placeholder="请输入手机号" />
        </div>
        <div class="label">验证码</div>
        <div class="formItem">
            <input type="text" placeholder="请输入验证码" />
            <button type="button" :disabled="codeDisabled" :class="{disabled: codeDisabled}" class="getCode" @click="getCode">{{codeText}}</button>
        </div>
        <button type="button" class="btn disabled">我要报名</button>
        <Popup v-model="area" position="bottom">
            <Picker :columns="areaData" show-toolbar @confirm="onChangeArea" @cancel="area = false"/>
        </Popup>
        <Popup v-model="shop" position="bottom">
            <Picker :columns="shopData" show-toolbar @confirm="onChangeShop" @cancel="shop = false"/>
        </Popup>
    </div>
</template>

<script>
    import { Popup, Picker } from "vant";
    export default {
        name: "registration",
        components: {
            Popup,
            Picker
        },
        data () {
            return {
                codeText: '获取验证码',
                area: false,
                areaData: ['杭州', '宁波', '温州', '嘉兴', '湖州'],
                shop: false,
                shopData: ['门店1', '门店2' ,'门店3' ,'门店4'],
                codeDisabled: false
            }
        },
        methods: {
            selectArea () {
                this.area = !this.area
            },
            onChangeArea (value, index) {
                this.area = false
            },
            selectShop () {
                this.shop = !this.shop
            },
            onChangeShop (value, index) {
                this.shop = false
            },
            getCode () {
                let t = 11
                this.codeDisabled = true
                let inter = setInterval( () => {
                    t--;
                    this.codeText = t + 's';
                    if(t <= 0) {
                        this.codeDisabled = false
                        this.codeText = '获取验证码';
                        clearInterval(inter);
                    }
                }, 1000)
            }
        }
    }
</script>

<style scoped lang="scss">
    .container{
        padding: 0 16px;
    }
    .caption{
        font-size: 12px;
        color: #333333;
        font-weight: bold;
        margin: 50px auto;
        text-align: center;
    }
    .game{
        li{
            width: 225px;
            height: 45px;
            background-color: #F5F5F5;
            text-align: center;
            margin: 0 auto;
            line-height: 45px;
            border-radius: 30px;
            margin-bottom: 15px;
            font-size: 15px;
            color: #333333;
            font-weight: bold;
        }
        li.active{
            background-color: #5CB851;
            color: #ffffff;
        }
        li:last-child{
            margin-bottom: 51px;
        }
    }
    .area{
        font-size: 18px;
        color: #333333;
        display: flex;
        align-items: center;
        margin-bottom: 35px;
        .icon1{
            width: 17px;
            margin-right: 7px;
        }
        .icon2{
            width: 12px;
            margin-left: 8px;
        }
    }
    .select{
        height: 50px;
        border: 1px solid #E5E5E5;
        width: 100%;
        border-radius: 6px;
        font-size: 14px;
        color: #666666;
        padding-left: 24px;
        padding-right: 15px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        margin-bottom: 35px;
        .icon1{
            width: 17px;
        }
        .best{
            width: 32px;
            position: absolute;
            left: 0;
            top: 0;
        }
    }
    .formItem{
        margin-bottom: 35px;
        display: flex;
        border: 1px solid #E5E5E5;
        height: 50px;
        line-height: 50px;
        width: 100%;
        border-radius: 6px;
        padding: 0 24px;
        font-size: 14px;
        align-items: center;
        input{
            border: 0 none;
            color: #666666;
            width: 100%;
            height: 90%;
            outline: none;
        }
        .getCode{
            width: 130px;
            height: 30px;
            line-height: 30px;
            background-color: #5CB851;
            color: #ffffff;
            font-size: 11px;
            border: 0 none;
            border-radius: 4px;
        }
        .getCode.disabled{
            opacity: 0.4;
        }
    }
    .label{
        font-size: 15px;
        color: #333333;
        margin-bottom: 6px;
        font-weight: bold;
        line-height: 24px;
    }
    .btn{
        width: 100%;
        height: 50px;
        border: 0 none;
        border-radius: 8px;
        font-size: 17px;
        background: #5CB851;
        color: #ffffff;
        margin-bottom: 35px;
    }
    .btn.disabled{
        opacity: 0.4;
    }
</style>
